:root {
  --background-color-base: #1e1e1f;
  --background-color: #313031;
  --soft-background-color: #8e8e8e;
  --shadow-ligh: #f8f8f8;
  --color: #f8f8f8;
  --accent-color: #e9d2b0;
  --active-lamp: #f8f8f8;
  --active-lamp-label: #1e1e1f;
  --tab-border-radius: 3px;
  --tab-active-background-color: #403f40;
  --shadow-general-container: inset 0px 0px 3px 0px #000, 0px -2px 2px -1px #000, -2px 0px 2px -2px #0f0d0c, 2px 0px 2px -2px #0f0d0c, 0px 2px 2px -2px #ebe6e4;
  --shadow-up: 0px 0px 8px 0px var(--background-color-base);
  --selected: orange;
}

html, body {
  font-family: Titillium Web;
}

body {
  background-color: var(--background-color-base);
  color: var(--color);
}

h1 {
  text-align: center;
}

#general-info {
  justify-content: space-between;
  min-height: 600px;
  display: flex;
}

#matrix-drawing {
  -webkit-user-select: none;
  user-select: none;
  border: 1px solid #000;
  flex: 1;
  grid-area: matrixDrawing;
  height: fit-content;
  margin: auto;
}

.row, .mini-row {
  display: flex;
}

.square {
  aspect-ratio: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-width: 40px;
  display: flex;
}

.square.active label {
  color: var(--active-lamp-label);
}

.square label {
  pointer-events: none;
}

.mini-square {
  width: 10px;
  height: 10px;
}

.active {
  background-color: var(--active-lamp);
}

.list-element {
  display: flex;
}

.list-element label {
  font-weight: 800;
}

#available-shifts {
  background-color: var(--tab-active-background-color);
  box-shadow: var(--shadow-general-container), inset 0px -4px 2px 0px var(--background-color-base);
  justify-content: space-between;
  padding: 12px;
  font-size: 48px;
  font-weight: bold;
  display: flex;
}

#available-shifts .current {
  color: var(--background-color-base);
  background-color: var(--selected);
  box-shadow: 0 0 3px #000, 0 -2px 2px -1px #000, -2px 0 2px -2px #0f0d0c, 2px 0 2px -2px #0f0d0c, 0 2px 2px -2px #ebe6e4;
}

.shift-control {
  border-radius: var(--tab-border-radius);
  padding: 12px;
  box-shadow: inset 0 0 3px #000, 0 -2px 2px -1px #000, -2px 0 2px -2px #0f0d0c, 2px 0 2px -2px #0f0d0c, 0 2px 2px -2px #ebe6e4;
}

#available-shifts > div {
  flex-direction: column;
  align-items: center;
  display: flex;
}

#available-shifts * {
  cursor: pointer;
}

.mini-matrix {
  border: 1px solid;
}

#binary-representation {
  padding: 0;
  position: relative;
}

#segmented-display-controller {
  flex: 1;
}

#matrix-container {
  grid-template: "none sliderColumns" 25px
                 "sliderRows matrixDrawing"
                 / 25px auto;
  max-height: 800px;
  display: grid;
}

#slider-columns {
  grid-area: sliderColumns;
}

#slider-rows {
  grid-area: sliderRows;
}

#matrix-size-overlay {
  background-color: var(--tab-active-background-color);
  color: var(--color);
  grid-area: matrixDrawing;
  place-content: center;
  font-size: 128px;
  display: flex;
}

.hidden {
  display: none !important;
}

#matrix-size-overlay > span:first-child {
  align-items: center;
  display: flex;
}

.exponential-label {
  font-size: 8px;
  line-height: baseline;
  height: 10px;
}

nav {
  width: 100%;
  display: flex;
}

nav ul {
  margin: 0;
  transform: translateY(2px);
}

nav ul, li {
  justify-content: space-around;
  align-items: center;
  width: 100%;
  text-decoration: none;
  list-style: none;
  display: flex;
}

nav li {
  cursor: pointer;
  border-radius: var(--tab-border-radius);
  background-color: var(--soft-background-color);
  padding: 20px;
}

nav li:first-child {
  box-shadow: inset 4px 4px 2px 0px var(--background-color-base);
}

nav li:last-child {
  box-shadow: inset -4px 4px 2px 0px var(--background-color-base);
}

nav li.current {
  background-color: var(--tab-active-background-color);
  color: var(--accent-color);
}

.menu {
  background-color: var(--background-color);
  border-radius: var(--tab-border-radius);
  margin-top: 12px;
  padding: 12px;
  box-shadow: 0 0 4px 3px #000;
}

section {
  background-color: var(--background-color);
  border-radius: var(--tab-border-radius);
  flex: 1 0;
  padding: 12px;
  box-shadow: inset 0 0 3px #000, 0 -2px 2px -1px #000, -2px 0 2px -2px #0f0d0c, 2px 0 2px -2px #0f0d0c, 0 2px 2px -2px #ebe6e4;
}

#general-info {
  background-color: var(--tab-active-background-color);
  box-shadow: var(--shadow-general-container);
  gap: 12px;
  padding: 12px;
}

#tools {
  grid-template-rows: 40px 40px auto;
  grid-template-areas: "export import"
                       "lamp-type-label lamp-type"
                       "blueprint blueprint";
  display: grid;
}

#tools button {
  cursor: pointer;
  z-index: 1;
  color: #a7a7a7;
  white-space: nowrap;
  background-color: #313131;
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  padding: 8px 12px;
  font-weight: 600;
  display: inline-block;
  box-shadow: inset 0 -4px .5px -2px #121212, inset 5px 0 2px -2.5px #1f1f1f, inset -3px 0 1px -1.5px #292929, inset 0 4px 1px -2px #515050;
}

#tools button:hover {
  color: #604814;
  background-color: #ffa900;
  text-decoration: none;
  box-shadow: inset 0 -4px .5px -2px #3e2200, inset 5px 0 2px -2.5px #ff9d02, inset -3px 0 1px -1.5px #a46200, inset 0 4px 1px -2px #c08d44, 0 0 4px 2px #805c10;
}

#lamp-type-label {
  height: 100%;
  vertical-align: center;
  grid-area: lamp-type-label;
  align-items: center;
  display: flex;
}

#import {
  grid-area: import;
}

#export {
  grid-area: export;
}

#blueprint-input {
  vertical-align: baseline;
  background: #8e8e8e;
  border: none;
  border-radius: 4px;
  grid-area: blueprint;
  max-width: 100%;
  padding: 6px;
  font-family: inherit;
  font-size: 105%;
  line-height: 1.2;
  box-shadow: inset 0 4px 1px -2px #000, inset 0 -4px 1px -2px #c5c5c5, inset 2px 0 1px #5f5f5f, inset -2px 0 1px #5f5f5f, inset 0 -2px 2px #5f5f5f, 0 0 4px 1px #2e2521;
}

#blueprint-input:focus {
  background: #f0dab4;
  box-shadow: inset 0 4px 2px -2px #000, inset 0 -1px 1px #74624b, inset 0 -4px 2px -2px #e0e0e0, inset 2px 0 2px #a6885c, inset -2px 0 2px #a6885c, 0 0 4px 1px #2e2521;
}
